$medium_font: 1.5rem;
$large_font: 2.25rem;
$disabled_colour: #d5d5d5;

html {
    background: #F7F7F7;
    font-size: 16px;
}
body * { font-family: 'Open Sans', sans-serif; color:#000000DE;}

.icon {font-size:16px; cursor: pointer;}
.field input, .field select {border:1px solid #D5D5D5; padding:5px}
.field select {width:200px}
label {font-weight:bold}

.hide { display:none;}
.App {
    display: flex;
    flex-flow: row wrap;
    align-items: center;
}

.App > * {
    flex: 1 100%;
  }

/* Header */
header {background-color:#2196F3;  padding:0 72px; height:56px;}
header h1{color:#fff; font-size:20px}

/* Form Name section */
#form-name-section {
    position: relative;
    border-bottom:1px solid #D5D5D5; 
    line-height: 96px; 
    background-color: #fff; 
    padding: 0 72px;
}
.default {
    color: #D5D5D5;
}
.section-title {
    display: flex;
    flex-direction: row;
    background-color: #fff;
    margin: 0 auto;
    width: 60%;
    min-width: 700px;

    .current-section {
        color: #fff;
        background-color: #595959;
        font-size: $medium_font;
        padding: 1rem;
    }

    .section-buttons {
        flex: 1;
        display: flex;
        flex-direction: column;

        .row {
            display: flex;
            align-items: center;
            justify-content: flex-end;
            flex: 1;
        }

        button {
            border: none;
            background: none;

            i {
                font-size: $medium_font;
            }

            &:disabled {
                i {
                    color: $disabled_colour;
                }
            }
        }
    }

    .name-section {
        display: flex;
        align-items: center;
        margin-left: 1rem;

        input {
            font-size: $medium_font;
            border: none;
        }
    }
}
#form-name { font-size: $medium_font; border:0 none; margin:0 }

/* Edit mode */
.field {
    background:#fff; 
    border:1px solid #D5D5D5; 
    padding:30px;
    margin-bottom: 1rem;

    h3 {
        font-size: $large_font;
    }
}
.field input {border:0 none; border-bottom:1px solid #D5D5D5; height:40px; width:100%}
.add-button {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    margin:10px 0;

    button {
        display: flex;
        align-items: center;

        cursor: pointer;
        border: none; background:none;
        .icon {
            font-size: $medium_font;
            padding-right: 5px;
        }

        span {
            font-size: 1rem;
            font-weight: bold;
        }
    }
}

.remove button{border:0 none; background:none;}
.editable-functions { text-align:right; padding:10px 0;}
.editable-functions div.type  {float:left; }
.editable-functions .float-right {float:right;}
.editable-functions .float-right div{  display:inline-block; padding:0 10px}


/* toggle switch */
.required {  width:40px; border-right:1px solid #ddd;position: relative; }
.required input[type=checkbox] {height: 0; width: 0; visibility: hidden;}
.required input:checked+label {background: #69B74A;}
.required label:active:after {width: 10px;}
.required input:checked+label:after {left: calc(100% - 5px); transform: translateX(-100%);}
.required label {
    text-align:left;
    cursor: pointer;
    text-indent: -75px;
    width: 40px;
    height: 20px;
    background: grey;
    display: block;
    border-radius: 100px;
    position: absolute;
    top:0
}

.required label:after {
    content: '';
    position: absolute;
    top: 5px;
    left: 5px;
    width: 10px;
    height: 10px;
    background: #fff;
    border-radius: 90px;
    transition: 0.3s;
}





.content {
    flex: 1;
    padding:0;

    & > * {
        margin-bottom: 1rem;
    }
}

.section {
    margin: 0 auto; 
    width: 60%;
    min-width: 700px;
}
.display-mode {
   position: relative;
}

.error {
    color: red;
    position:absolute;
    right:0;
}

i.fa {
    cursor: pointer;
}

.save-message {
    position: absolute;
    top: 0;
    right: 0;
    line-height: initial;
    height: 16px;
    padding: 8px;
    margin: 8px;
    background-color: #cde2a9;
    color: #4e633a;
    font-size: 12px;
    font-style: italic;
    font-weight: bold;
}